
@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,700");
html,body{height:100%
}
body{margin:0 auto;font-size:16px
}
.m__background{position:fixed;content:"";height:100%;width:100%;z-index:-1;height:100%;top:0;background-image:url("../assets/background.jpg");background-repeat:no-repeat;background-size:cover;background-position:50% 50%
}
.m__background:after{position:absolute;top:0;content:"";height:100%;width:100%;background-color:rgba(6,214,160,0.9)
}
h1,h2,h3,h4,h5{margin:0
}
ul,ol{margin:0;padding:0
}
.m__menu{width:100%;background:#f2f2f2;position:fixed;bottom:0;z-index:+2
}
.m__menu__list{height:50px;margin:0;padding:0;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
@media (min-width: 680px){
.m__menu__list{float:right;width:400px;height:60px
}
}
.m__menu__item{-ms-flex:1 0 auto;flex:1 0 auto;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center
}
.m__menu__item:hover{cursor:pointer
}
.m__menu__item__icon{height:30px;width:30px
}
.m__menu__item__icon--active path{fill:#ef476f
}



