
@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,700");
html,body{height:100%
}
body{margin:0 auto;font-size:16px
}
.m__background{position:fixed;content:"";height:100%;width:100%;z-index:-1;height:100%;background-image:url("../assets/background.jpg");background-repeat:no-repeat;background-size:cover;background-position:50% 50%
}
.m__background:after{position:absolute;top:0;content:"";height:100%;width:100%;background-color:rgba(6,214,160,0.9)
}
h1,h2,h3,h4,h5{margin:0
}
ul,ol{margin:0;padding:0
}
.m__search{height:calc(100vh - 50px)
}
@media (min-width: 680px){
.m__search{height:calc(100vh - 60px)
}
}
.m__search__filter{width:100%;height:50px;display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;-ms-flex-align:center;align-items:center
}
.m__search__filter__icon{background:transparent;border:none;outline:none
}
.m__search__filter__icon svg{margin-right:1rem;height:25px;width:25px
}
.m__search__filter__icon svg path{fill:rgba(255,255,255,0.75)
}
.m__search__filter__icon:hover{cursor:pointer
}
.m__search__results{width:100%;height:calc(100% - 50px);overflow-y:auto;-webkit-overflow-scrolling:touch
}
@media (min-width: 680px){
.m__search__results{height:calc(100% - 60px)
}
}
.m__search__results__profile{position:relative;background:#fff;height:auto;min-height:calc(100% - 1rem);width:calc(100% - 2rem);max-width:350px;margin:0 auto;border-radius:10px
}
.m__search__results__profile__header{position:relative;border-radius:10px 10px 0px 0px;background:linear-gradient(45deg, #2bc098, #24a07f);height:80px
}
.m__search__results__profile__face{position:absolute;margin:0 0 0 1rem;bottom:calc(-80px/2);height:80px;width:80px;border-radius:10px
}
.m__search__results__profile__connect{position:absolute;right:1rem;top:50%;transform:translateY(-50%)
}
.m__search__results__profile__connect:link,.m__search__results__profile__connect:visited,.m__search__results__profile__connect:active{font-family:"Lato",sans-serif;text-transform:uppercase;font-size:0.75rem;text-decoration:none;padding:0.5rem 1.5rem;color:#fff;border:solid 1px #fff;border-radius:5px;transition:background 0.3s ease-in, color 0.3s ease-in
}
.m__search__results__profile__connect:hover{background:rgba(6,214,160,0.5)
}
.m__search__results__profile__info{padding:1rem 1.5rem;color:#4d4d4d;font-family:"Lato",sans-serif;text-align:right;border-bottom:solid 1px #f2f2f2
}
.m__search__results__profile__info__name{margin:2rem 0 0.25rem 0;text-transform:uppercase;font-size:1rem;letter-spacing:1px
}
.m__search__results__profile__info__job{text-transform:uppercase;font-size:0.75rem;font-weight:400;letter-spacing:0.5px
}
.m__search__results__profile__info__description{font-size:0.75rem;color:#999
}
.m__search__results__profile__skills__type{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center
}
.m__search__results__profile__skills__type__item{color:#999;font-family:"Lato",sans-serif;text-transform:uppercase;margin:1rem 1rem 0.5rem 1rem;padding-bottom:1rem;font-size:1rem
}
.m__search__results__profile__skills__type__item--active{color:#4d4d4d;border-bottom:solid 1px #4d4d4d
}
.m__search__results__profile__skills__list{font-family:"Lato",sans-serif;color:#8c8c8c;font-size:0.75rem;margin:0.5rem;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:center;justify-content:center
}
.m__search__results__profile__skills__list__item{text-transform:uppercase;text-align:center;padding:0.5rem 1rem;margin:0.5rem;border:solid 1px #d9d9d9;border-radius:5px;list-style:none
}
.m__search__filters{background:#fff;position:absolute;z-index:+2;height:100vh;width:100%
}
.m__search__filters__x{height:40px;width:40px;right:1rem;top:1rem;position:absolute;border:none;background:transparent
}
.m__search__filters__x svg path{fill:#bfbfbf
}
.m__search__filters__x:hover{cursor:pointer
}
.m__search__filters__x:hover svg path{fill:#4d4d4d
}
.m__search__filters__form{max-width:350px;height:100vh;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;margin:0 auto
}
.m__search__filters__form__input{font-family:"Lato",sans-serif;text-transform:uppercase;background:transparent;font-size:1rem;border:none;color:#bfbfbf;border-bottom:solid 1px #bfbfbf;margin:3rem 0;padding:0 0 2rem 2rem
}
.m__search__filters__form__input:focus{color:#4d4d4d;border-bottom:solid 1px #4d4d4d;outline:0
}
.m__search__filters__form__input::-webkit-input-placeholder{color:#bfbfbf
}
.m__search__filters__form__radio{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row
}
.m__search__filters__form__radio__item{-ms-flex-preferred-size:50%;flex-basis:50%;color:#bfbfbf;text-align:center;padding:1rem 2rem;font-family:"Lato",sans-serif;text-transform:uppercase;font-size:1rem;transition:background 0.3s ease-in
}
.m__search__filters__form__radio__item:first-child{margin:1rem 1rem 1rem 0
}
.m__search__filters__form__radio__item:last-child{margin:1rem 0 1rem 1rem
}
.m__search__filters__form__radio__item:hover{cursor:pointer;background:#f2f2f2
}
.m__search__filters__form__radio__item--active{color:#4d4d4d;border:solid 1px #4d4d4d
}
.m__search__filters__form__go{height:40px;width:40px;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;background:transparent;outline:0;border:none;transition:transform 0.3s ease-in
}
.m__search__filters__form__go:hover{cursor:pointer;transform:translateX(0.5rem)
}
.m__search__filters__form__go:hover svg path{fill:#eb184a
}
.m__search__filters__form__go svg path{fill:#ef476f
}



